<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process14"></a>

<p><br></p>
<p><span class="rvts17">蜥蜴（Lizard）:</span></p>
<p><img width="440" height="616" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber48.jpg"><span class="rvts18"></span></p>
<p><span class="rvts43"></span>
<br><span class="rvts21">远古帝国的蜥蜴人是有教养的贵族，源力用于强化那些遵守规矩的人。他们自恃为杰出精英，看待外来者如同仆人。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">种族天赋：</span></p>
<div><table width="1206" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="284" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">图</span><span class="rvts45">标</span></p>
</td>
<td width="208" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">英文</span></p>
</td>
<td width="144" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">中文</span></p>
</td>
<td width="515" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">说明</span></p>
</td>
</tr>
<tr valign="top">
<td width="284" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber49.jpg"></p>
</td>
<td width="208" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Sophisticated</span></p>
</td>
<td width="144" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">久经事故</span></p>
</td>
<td width="515" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">+10%</span><span class="rvts21">火焰抵抗，</span><span class="rvts20">+10%</span><span class="rvts21">毒素抵抗</span></p>
</td>
</tr>
<tr valign="top">
<td width="284" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="128" height="128" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber50.jpg"></p>
</td>
<td width="208" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Spellsong</span></p>
</td>
<td width="144" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">咒语歌</span></p>
</td>
<td width="515" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">+1</span><span class="rvts21">劝告</span></p>
</td>
</tr>
</table>
</div>
<p><span class="rvts43"></span>
<br><span class="rvts29">特殊技能：</span></p>
<div><table width="1206" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="120" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">图</span><span class="rvts45">标</span></p>
</td>
<td width="254" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">英文</span></p>
</td>
<td width="120" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">中文</span></p>
</td>
<td width="656" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">说明</span></p>
</td>
</tr>
<tr valign="top">
<td width="120" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber51.jpg"></p>
</td>
<td width="254" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Dragon's Blade</span></p>
</td>
<td width="120" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">火焰龙息</span></p>
</td>
<td width="656" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">对前方造成火焰伤害，并在身后留下一条火焰之径</span></p>
</td>
</tr>
<tr valign="top">
<td width="120" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber52.jpg"></p>
</td>
<td width="254" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Dome of Protection</span></p>
</td>
<td width="120" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">守护圆罩</span></p>
</td>
<td width="656" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">提升护罩内队友的物理和法术防御值</span></p>
</td>
</tr>
</table>
</div>
<p class="rvps2"><span class="rvts43"></span>
<br><span class="rvts29">使用技巧：</span>
<br><span class="rvts21">蜥蜴种族具备额外的火焰和毒素抵抗，另外他们在初期也有额外的一点劝告加成。蜥蜴拥有挖土技能，因此玩家不必装备铲子便可以进行挖掘工作。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



